// ====================================
// Article page's style
// ====================================
/*
* TABLE OF CONTENTS
*
* header
* social floating bar
* meta
* sapo
* body
*    -> caption
*    -> timeline block
* tag
*/
// end demo style
.clearfix {
    clear: both;
}

.article__comment .main-input p.note {
    float: none;
}

.modal-lg {
    width: 322px;
}

div.modal-dialog.modal-lg div.modal-content {
    padding: 10px;
    width: inherit !important;
}

div.modal-dialog.modal-lg div.modal-content:first-child div {
    width: 305px;
}

.modal-dialog.modal-lg .modal-content .form-group {
    margin-bottom: 15px;
}

.modal-dialog.modal-lg .modal-content .form-group label {
    margin-bottom: 5px;
}

body.modal-open {
    overflow: inherit;
}

.modal-lg .form-control {
    display: inline-block;
}

.site-header {
    background-color: #FF6600;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;

    div[class^="col"] {
        padding: 0;
    }

    .aside-header {
        flex: 0 0 50px;

        @include media-breakpoint-up(lg) {
            flex: 0 0 90px;
        }

        font-size: 0;
    }

    .main-header {
        display: flex;
        align-items: center;
        height: 50px;
        padding: 0 10px 0 5px !important;

        @include media-breakpoint-up(lg) {
            height: 90px;
        }

        justify-content: space-between;
    }

    .go-back {
        width: 55px;
        height: 50px;

        @include media-breakpoint-up(lg) {
            width: 90px;
            height: 90px;
        }

        border: none;

        &.has-touched,
        &:hover {
            background-color: darken(#FF6600, 10%);
        }

        @include media-breakpoint-down(md) {
            i {
                transform: scale(0.5);
                margin-left: -15px;
                margin-top: -20px;
            }
        }
    }

    .logo {
        display: inline-block;
        width: 40px;
        height: 45px;

        @include media-breakpoint-up(lg) {
            width: 205px;
            height: 50px;
        }

        margin-right: 10px;
        margin-top: 5px;
        background: url(../img/kt-logo.png) 0 0 no-repeat;
    }

    .topic-title {
        font-size: 2.4rem;
        text-align: center;
        font-family: $font-family-serif;
        text-transform: uppercase;

        a {
            color: #fff;
        }

        @include media-breakpoint-down(sm) {
            display: none;
        }
    }

    .group-title {
        @include media-breakpoint-down(lg) {
            display: none;
        }
    }

    .group-link {
        margin-top: 8px;

        a {
            display: inline-block;
            padding: 3px 20px;
            border-radius: 3px;
            color: #fff;

            @include media-breakpoint-up(md) {
                margin: 0 20px;
            }
        }

        a:hover,
        .active {
            // border: 1px solid darken($primary, 10%);
            background-color: rgba(black, .1);
        }

        @include media-breakpoint-down(md) {
            width: 120px;
        }
    }

    .social-comment {
        margin-left: 10px;
        width: 100px;

        @include media-breakpoint-up(md) {
            width: 100px;
        }

        height: 50px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        a {
            width: 30px;
            height: 30px;
            background-color: #0C4C9B;
            border-radius: 3px;
            display: flex;
            justify-content: center;
            align-items: center;

            i {
                color: #fff;
            }
        }
    }
}

.article {
    margin-top: 50px;

    @include media-breakpoint-up(lg) {
        margin-top: 90px;
    }

    @include clearfix();

    &__cover {
        margin-bottom: $spacer-3;

        @include media-breakpoint-up(sm) {
            margin-bottom: $spacer-5;
        }

        text-align: center;
    }

    /*> header*/
    &__header {
        @include clearfix();

        h1 {
            font-size: 1.8rem;
        }

        @include media-breakpoint-up(sm) {
            h1 {
                @include font-style(32, 42);
            }
        }

        color: black;
        text-align: center;
        text-transform: uppercase;
        font-family: $font-family-serif;
    }

    .body-author {
        font-weight: bold;
        text-align: right;
        // @include media-breakpoint-up(md) {
        //     width: 64rem;
        // }
        font-size: 1.6rem;
        margin: $spacer-4 auto 0;
    }

    /*> sapo*/
    &__sapo {
        font-size: 1.4rem;
        margin-top: $spacer-3;

        @include media-breakpoint-up(sm) {
            margin-top: $spacer-5;
            @include font-style(18, 26);
        }

        font-weight: bold;
    }

    /*> body*/
    &__body-bak {
        // Note: padding, margin, fontstyle among article body
        overflow: hidden;
        overflow-wrap: break-word;
        @include clearfix();
        color: #333;

        // >* {
        //     max-width: 100%;
        // }
        p,
        .body-text,
        h3,
        h4,
        h5 {
            margin-top: $spacer-5;
            font-size: 1.4rem;

            @include media-breakpoint-up(sm) {
                margin-top: $spacer-5;
                @include font-style(18, 26);
            }
        }

        ins {
            text-decoration: none;
        }

        img {
            max-width: 100%;
            margin: auto;
            transform: translate3d(0, 0, 0);
        }

        table {
            margin: auto;
            max-width: 40rem;
        }

        .sub-heading {
            font-family: $sedgwick_Ave_Display;
            font-weight: normal;
            display: block;
            margin-bottom: 10px;

            @include media-breakpoint-up(sm) {
                @include font-style(60, 80);
            }
        }

        .body-text {
            margin: $spacer-5 auto 0;
            justify-content: space-between;
        }

        .body-image {
            .photo {
                position: relative;
                display: block;
            }

            .caption {
                display: block;
                font-style: italic;
                margin-top: $spacer-3;
                color: #999;
            }

            &.img-fullSize {
                text-align: center;
                @include clearfix();
            }

            &.float-md-left,
            &.float-md-right,
                {
                @include media-breakpoint-up(md) {
                    width: 36rem;
                }

                margin-bottom: $spacer-3;
            }

            @include media-breakpoint-up(md) {

                &.float-md-left,
                    {
                    margin-left: -10rem;
                    margin-right: 3rem;
                }

                &.float-md-right,
                    {
                    margin-right: -10rem;
                    margin-left: 3rem;
                }
            }
        }

        .body-video-youtube {
            text-align: center;

            iframe {
                width: 61rem;
                height: 32rem;
                max-width: 100%;
            }
        }
    }

    &__body {
        // Note: padding, margin, fontstyle among article body
        overflow: hidden;
        overflow-wrap: break-word;
        margin-top: 10px;
        @include clearfix();
        @include font-style(18, 24, normal);

        span,
        em,
        i,
        strong,
        b {
            font-size: initial !important;
        }

        .col {

            >.sub-heading,
            >p,
            >div,
            >h3,
            >h4,
            >h5 {
                margin-top: 20px;
            }

            >p,
            >div {
                @include font-style(18, 26, normal);
            }

            ins {
                text-decoration: none;
            }

            img {
                max-width: 100%;
                display: block;
                margin: auto;
                transform: translate3d(0, 0, 0);
            }

            table {
                margin: auto;
                max-width: 100%;
            }
        }

        .article-related-listing {
            margin-top: 20px;
            padding: 16px;
            background-color: #F7F7F7;

            .label {
                text-transform: uppercase;
                color: $primary;
                font-weight: bold;
            }

            li+li {
                margin-top: 8px;
            }

            a {
                @include font-style(16, 22, normal);
                padding-left: 16px;
                position: relative;
                display: block;

                &:before {
                    content: "";
                    display: block;
                    width: 4px;
                    height: 4px;
                    background-color: #000;
                    position: absolute;
                    left: 0;
                    top: 9px;
                    border-radius: 50%;
                }
            }
        }

        .article-inner-story {
            .story {
                @include clearfix();
                background-color: rgba($primary, .1);
                margin-top: 10px;

                &:first-child {
                    margin-top: 0;
                }

                &__thumb {
                    width: 33%;
                    height: 0;
                    padding-bottom: 24.5%;
                    overflow: hidden;
                    float: left;
                    clear: left;
                    margin: 0;
                    background-color: #d7d7d7;
                    border-radius: 3px;
                    position: relative;
                    margin-right: 15px;
                }

                &__heading {
                    @include font-style(20, 28);
                    padding-top: 15px;
                    padding-right: 15px;
                }

                &__meta {
                    @include font-style(13, 18, normal);
                    margin-top: $spacer-1;
                }

                &__summary {
                    display: none;
                }
            }
        }

        .sub-heading {
            font-family: $sedgwick_Ave_Display;
            font-weight: normal;
            display: block;
            margin-bottom: 10px;

            @include media-breakpoint-up(sm) {
                @include font-style(60, 80);
            }
        }

        .pull-left {
            width: 50%;
            float: left;
            margin: 5px 20px 10px 0;
        }

        .pull-right {
            width: 50%;
            float: right;
            margin: 5px 0px 10px 20px;
        }

        .body-image {
            text-align: center;

            .photo {
                display: inline-block;
                position: relative;
            }

            .photo:hover {
                &:after {
                    background-color: rgba(47, 161, 179, .5);
                }
            }

            .caption {
                display: block;
                font-style: italic;
                margin-top: $spacer-3;
                color: #999;
            }
        }

        .body-quote {
            padding: 16px;
            border: 2px solid $primary;
            @include border-radius;
            padding: 30px 20px;
            position: relative;

            i {
                padding: 5px;
                background: #ffe1cd;
                font-size: 20px;
                position: absolute;
                top: -16px;
                left: -16px;
                color: $primary;
            }

            .text {}

            .author {
                font-style: normal;
                text-align: left;
                font-weight: bold;
                display: block;
                padding-top: 25px;
                position: relative;

                &:before {
                    background-color: $gray-500;
                    content: '';
                    width: 60%;
                    height: 1px;
                    position: absolute;
                    top: 15px;
                    left: 0px;
                }
            }

            img {
                width: 100px;
                height: 100px;
                float: right;
                margin: 0 0 10px 20px;
            }
        }

        .body-notification {
            background-color: rgba($primary, .05);
            border: 1px dashed $primary;
            padding: $spacer-4;
            @include border-radius;
        }

        .body-author {
            text-align: right;
            font-weight: bold;
        }

        .body-source {
            margin-top: 0 !important;
            text-align: right;
        }

        .group-img-fullsize {}
    }

    .style-1 {
        display: flex;
        justify-content: space-between;

        p {
            flex-grow: 1;

            img {
                border: 1px solid #979797;
                border-left: none;
                width: 100%;
            }

            &:first-child {
                flex-grow: 2;

                img {
                    border-left: 1px solid #979797;
                }
            }
        }
    }

    .style-2 {
        display: flex;
        justify-content: space-between;

        p {
            width: 25%;

            img {
                border: 1px solid #979797;
                border-left: none;
                width: 100%;
            }

            &:first-child {
                width: 50%;

                img {
                    border-left: 1px solid #979797;
                }
            }
        }
    }

    [class*="style-"] {
        &+.caption {
            display: block;
            font-style: italic;
            margin-top: 1rem;
            text-align: center;
            color: #999;
        }
    }

    &__comment {
        margin-top: 20px;
        position: relative;
        @include clearfix;

        .col {
            padding-top: 20px;
            border-top: 1px solid $gray-700;
        }

        .article__social {
            top: 23px;
        }

        .box-heading {
            h3 {
                @include font-style(20, 34);
                text-transform: uppercase;
            }
        }

        .main-input {
            @include clearfix();
            margin-top: 5px;

            textarea {
                border-radius: 3px;
                border: 1px solid #ddd;
                width: 100%;
                height: 70px;
                padding: 10px;
            }

            p.note {
                font-style: italic;
                float: left;
                display: inline-block;
                color: #999;
                @include font-style(12, 28, normal);
            }
        }

        .main-comment {
            margin-top: 20px;

            .avatar-user {
                width: 60px;
                height: 60px;
                background-color: darken(#FFF0E6, 10%);
                @include border-radius;
                display: flex;
                justify-content: center;
                align-items: center;

                i {
                    font-size: 30px;
                }
            }

            .media-body {
                >h5 {
                    color: $primary;
                    @include font-style(16, 20);
                    margin-bottom: 0;
                }

                >span {
                    @include font-style(14, 18, normal);
                }

                .comment-actions {
                    margin-top: 5px;

                    >* {
                        display: inline-block;
                        margin-right: 15px;
                    }

                    font-size: 1.2rem;

                    .cmt-like {
                        color: $primary;

                        &:hover {
                            color: $secondary;
                        }

                        i {
                            font-size: 1.1rem;
                            margin-right: 3px;
                            display: none;
                        }

                        span {
                            &:before {
                                content: 'Thích';
                            }
                        }

                        &.is-liked {
                            i {
                                display: inline-block;
                                color: $gray-400;
                            }

                            span {
                                color: $gray-600;

                                &:before {
                                    content: 'Đã thích';
                                }
                            }
                        }
                    }

                    .cmt-reply {
                        color: $primary;

                        &:hover {
                            color: $secondary;
                        }
                    }

                    .cmt-count {
                        i {
                            font-size: 1.1rem;
                            margin-right: 3px;
                        }
                    }

                    .cmt-like {}
                }

                .comment-input {
                    margin-top: 8px;

                    textarea {
                        border-radius: 3px;
                        border: 1px solid #ddd;
                        width: 100%;
                        height: 40px;
                        padding: 10px;
                    }
                }
            }

            .media {
                margin-top: 25px;
                padding-top: 25px;
                border-top: 1px dashed $gray-500;

                &:first-child {
                    margin-top: 0;
                    padding-top: 0;
                    border-top: none;
                }

                .media {
                    padding-top: 15px;

                    &+.media {
                        border-top: none;
                        margin-top: 25px;
                        padding-top: 0;
                    }

                    .avatar-user {
                        width: 40px;
                        height: 40px;

                        i {
                            font-size: 16px;
                        }
                    }

                    .media-body {
                        >h5 {
                            @include font-style(13, 18);
                            margin-bottom: 0;
                        }

                        >span {
                            @include font-style(12, 14, normal);
                        }
                    }
                }
            }
        }
    }
}

.article-control {
    display: flex;
    justify-content: space-between;
    height: 16.5rem;
    align-items: center;

    @include media-breakpoint-down(md) {
        height: 10rem;
    }

    border-top: 1px solid $gray-500;
    margin-top: $spacer-5;

    .prev,
    .next,
        {
        display: flex;

        .text {
            display: flex;
            flex-flow: column wrap;
        }

        @include media-breakpoint-up(md) {
            width: 50%;
        }

        span {}

        @include media-breakpoint-down(md) {
            i {
                width: 32px;
                height: 32px;
                background-size: 33px 666px;
            }
        }

        &:hover {
            background-color: rgba(black, .05);
        }
    }

    .prev {
        i {
            margin-right: 20px;

            @include media-breakpoint-down(md) {
                margin-right: 0px;
            }
        }
    }

    .next {
        i {
            margin-left: 20px;

            @include media-breakpoint-down(md) {
                margin-left: 0px;
            }
        }

        span {
            text-align: right;
        }

        flex-direction: row-reverse;
    }

    @include media-breakpoint-down(md) {
        .icon--mix-prev {
            background-position: -0px -83px;
        }

        .icon--mix-next {
            background-position: -0px -116px;
        }
    }

    .main-title {
        font-size: 2.4rem;
        line-height: 3.2rem;

        @include media-breakpoint-up(md) {
            line-height: 4rem;
            margin-top: $spacer-3;
        }

        font-family: $font-family-serif;
        text-transform: uppercase;

        @include media-breakpoint-down(md) {
            font-size: 1.6rem;
        }
    }

    .sub-title {
        font-family: playfair-regular;
        font-size: 1.6rem;

        @include media-breakpoint-down(md) {
            display: none;
        }
    }
}

.menu-verticle {
    left: -320px;
    width: 300px;
    height: 100%;
    overflow: scroll;
    position: fixed;
    top: 50px;

    @include media-breakpoint-up(lg) {
        top: 90px;
    }

    background-color: #fff;
    box-shadow: 5px 0 10px rgba(black, .3);
    z-index: 100;
    padding-bottom: 90px;

    ul {}

    li {
        a {
            display: block;
            padding: 15px;
            border-top: 1px solid $gray-500;
            text-transform: uppercase;
            font-weight: bold;
        }

        &:first-child {
            a {
                border-top: none;
            }
        }
    }

    &.is-active {
        left: 0px;
        transition: left .2s linear;
    }
}

.mask-is-open {
    &:before {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        content: '';
        background-color: rgba(black, .5);
    }
}

.article.inforgraphic {
    @include media-breakpoint-up(lg) {

        .article__header .col,
        .article__meta .col,
        // .article__sapo .col,
        // .article__comment .col,
        .article__tag .col,
            {
            margin: 0 280px;
        }

        .article__sapo {
            margin-top: 10px;
        }

        .article__body {
            .col {

                >div,
                >h3,
                >p {
                    width: 640px;
                    margin: 20px auto 0;
                }

                .body-image,
                .body-quote,
                .body-notification,
                .article-inner-story,
                .article-related-listing,
                    {
                    max-width: 640px;
                    margin: 20px auto 0;
                }

                .pull-left,
                .pull-right {
                    width: 36rem;
                    margin-bottom: $spacer-3;
                }

                .pull-left {
                    margin-left: calc(50% - 400px);
                    margin-right: 3rem;
                    float: left;
                }

                .pull-right {
                    margin-right: calc(50% - 400px);
                    margin-left: 3rem;
                    float: right;
                }

                .body-image-fullSize {
                    text-align: center;
                }

                .body-image.fullsize {
                    max-width: inherit !important;
                    width: inherit !important;
                }

                .group-img-fullsize {
                    display: block;
                }

                .fullsize-container {
                    max-width: inherit !important;
                    width: inherit !important;

                    >div,
                    >h3,
                    >p {
                        width: 640px;
                        margin: 20px auto 0;
                    }
                }
            }
        }
    }
}

.site-footer {
    @include media-breakpoint-up(lg) {
        border-top: 1px solid #999;
        padding-top: 30px;
        margin-top: 30px;
        background-color: lighten(#ffe1cd, 15%);
        font-size: 13px;

        .container {
            width: 980px;
            max-width: inherit;

            .contact {
                padding-bottom: 20px;

                p {
                    margin-top: 5px;
                }

                display: flex;
                justify-content: space-between;

                >.floatleft:nth-child(1) {
                    width: 60%;
                }

                >.floatleft:nth-child(2) {
                    width: 40%;
                    text-align: right;
                }
            }
        }
    }

    @include media-breakpoint-down(md) {
        border-top: 1px solid #999;
        padding-top: 20px;
        padding-bottom: 20px;
        margin-top: 30px;
        background-color: lighten(#ffe1cd, 15%);
        font-size: 13px;
        text-align: center;
    }
}